import React from 'react';
import styled from 'styled-components';

import { ProductInfo } from '@t/components';
import LazyImage from '@/components/detail/LazyImage';

interface PreviewSectionProps {
  data: ProductInfo;
}

const Wrapper = styled.div`
  display: none;

  @media (max-width: 720px) {
    display: block;
    border-bottom: 1px solid #ddd;

    & div:last-child {
      margin-bottom: 60px;
    }
  }
`;

const PreviewTitle = styled.h3`
  font-size: 32px;
  margin: 0;
  margin-top: 60px;
  margin-left: 40px;
`;

const Image = styled.div`
  width: 640px;
  height: 360px;
  margin: 0 auto;
  margin-top: 40px;
`;

const MobilePreviewSection: React.FC<PreviewSectionProps> = ({ data }) => {
  const { name, id } = data;
  const src = require(`@/assets/images/preview/${id}_mobile.jpg`);

  return (
    <Wrapper>
      <PreviewTitle>{`${name} Preview`}</PreviewTitle>
      <Image>
        <LazyImage src={src} />
      </Image>
    </Wrapper>
  );
};

export default MobilePreviewSection;
